<template>
  <div class="container">
    <div class="row">
      <ul class="col-4">
      <li v-for="(item, index) in nav.navigation" class="col alert alert-warning text-center">
        <a href="#" style="text-decoration: none;" @click.prevent="description(index)" >{{item.title}}</a>
      </li>
      </ul>
      <thearticle class="col-8 alert alert-primary" v-if="typeof(index) === 'number'" :index="index"></thearticle>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import thearticle from '@/components/Article'
export default {
  name: 'nav',
  data () {
    return {
      num: 0
    }
  },
  computed: {
    ...mapGetters({
      nav: 'getNav',
      index: 'getIndex'
    })
  },
  methods: {
    description (index) {
      this.$store.dispatch('setIndex', index)
    }
  },
  components: {
    thearticle
  }
}
</script>

<style>
  li {
    list-style: none;
  }
</style>


