<template>
  <div class="container anim">
    <h1>{{nav.navigation[index].title}}</h1>
    <img :src="nav.navigation[index].src" alt="" align="left">

   <p v-for="(value, key, ind) in nav.navigation">
      <span v-if="index === key" v-for="(item, k) in nav.navigation[key].description">
       <strong>{{k}} - </strong> <span>{{item}}</span><br>
      </span>
    </p>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'thearticle',
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters({
      nav: 'getNav'
    })
  },
  props: {
    index: { type: Number, require: true }
  }
}
</script>

<style>
  img {
    width: 200px;
    height: 250px;
    margin-right: 15px;
    animation: rotate 1s linear;
  }
  p {
    color: black;
    animation: fadeIn 2s;
  }

 @keyframes rotate {
    100% { transform: rotate(360deg); }
  }
  @keyframes fadeIn {
  0% { opacity: 0; }
  20% { opacity: 0; }
  40% { opacity: 0.3; }
  60% { opacity: 0.5; }
  80% { opacity: 0.9; }
  100% { opacity: 1; }
 }
</style>



