/* eslint-disable no-trailing-spaces */
<template>
  <div id="app" class="container">
    <header>
      <div class="page-header text-center">
        <h1>My view for Vue.JS</h1>
      </div>
      <nav >
        <div class="row">
          <div v-for="item in mainNav" class="col menu text-center alert alert-warning">
            <router-link style="text-decoration: none;" :to="item.src">{{item.title}}</router-link>
          </div>
        </div>
      </nav>
    </header>
    <main>
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import axios from 'axios'
export default {
  name: 'app',
  created () {
    axios.get('https://shevchenkoalbert.github.io/testjson/project.json')
      .then((response) => {
        this.$store.dispatch('addNav', response.data)
      })
      .catch(function (error) {
        console.log(error)
      })
  },
  computed: {
    ...mapGetters({
      mainNav: 'getMainNav'
    })
  }
}
</script>

<style>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*main {
  text-align: center;
  margin-top: 40px;
}

header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #337ab7;
  color: #ffffff;
}

header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding-top: 16px;
}*/
 
.menu {
  list-style:  none;
  background-color: #337ab7;
  text-align: center;
}
a {
  color: #ff5722;
}
a:hover {
  color: #ff5722;
  font-size: 150%;
  transition: .5s; 
}
</style>



